@import url(https://fonts.googleapis.com/css?family=Dosis:200,300,400,500,600,700,800&display=swap&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Nunito:200,200i,300,300i,400,400i,600,600i,700,700i,800,800i,900,900i&display=swap&subset=latin-ext);
body{margin:0;font-family:'Dosis', 'Nunito', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.slider-container{background:url(/CMG/static/media/slider-bg.ae23a172.jpg) no-repeat;background-position:center;background-size:cover;position:relative;padding-top:100px;padding-bottom:50px;min-height:100vh}

.containerSlider{padding:40px;max-width:1170px;margin:0 auto}.green-text{color:#dcf836}.recomended{color:white;margin-bottom:50px}.recomended .description-wrapper{width:70%;margin-top:50px}.recomended h2{margin-top:0}.recomended .description{display:flex}.recomended button{font-family:"OpenSans", sans-serif;font-weight:600;background-color:#dd003f;color:#ffffff;padding:11px 25px;border-radius:20px;border:none;outline:none}.recomended .text{margin-left:20px}.recomended .text h4{margin-top:0}.slick-slide{position:relative;box-sizing:border-box;padding-right:15px;color:#ffffff}.slick-slide h6{font-family:"OpenSans", sans-serif;font-weight:700;clear:both;line-height:1}.slick-slide .title-in{background:rgba(0,0,0,0.7);padding:5px;display:flex;flex-direction:column;align-items:flex-start;margin:10px;width:calc(100% - 45px);bottom:0;left:0;position:absolute}.slick-slide .cate{margin-bottom:15px}.slick-slide .cate span{padding:3px 5px;border-radius:3px}.slick-slide .main-title{font-family:"Dosis", sans-serif;font-size:18px;color:#ffffff;font-weight:700;text-transform:uppercase;margin:0}.slick-slide .cate span .title{font-family:"Dosis", sans-serif;font-size:12px;color:black;font-weight:700;text-transform:uppercase}.slick-slide .cate .green{background-color:#dcf836}.slick-slide .movie-items .movie-item .title-in p{font-size:12px;color:#ffffff}.slick-slide .movie-items .movie-item .title-in p i{color:#f5b50a;font-size:22px}.slick-slide .movie-items .movie-item .title-in p span{font-weight:400;font-size:18px}.slick-slide .title-in p span{font-weight:400;font-size:18px;color:#dcf836}.slick-slide img{width:100%;height:auto;margin:auto}.slick-dots{display:flex !important;flex-direction:row-reverse}.slick-dots li button:before{font-size:10px;color:white;opacity:1}.slick-dots li.slick-active button:before{opacity:1;color:#dcf836}

footer{padding:20px;display:flex;flex-direction:column;text-align:center;background:#020d18}footer a{color:#dcf836}

